@charset "UTF-8";
.dish-list__add-to-cart {
  margin-left: 10px; }
  @media screen and (max-width: 768px) {
    .dish-list__add-to-cart {
      width: 35px;
      height: 35px; } }

@media screen and (max-width: 768px) {
  .dish-list__add-to-cart svg {
    font-size: 18px !important; } }

.dish-list__item {
  align-items: flex-start;
  margin-bottom: 30px;
  padding: 7px;
  min-height: 50px; }
  @media screen and (max-width: 768px) {
    .dish-list__item {
      font-size: 14px !important; } }
  .dish-list__item-published-mark {
    color: green;
    margin-left: 10px; }
  .dish-list__item-menu {
    position: absolute; }
  .dish-list__item .sizes-select {
    margin-left: 20px; }
  .dish-list__item-name {
    padding-left: 17px;
    display: flex;
    align-items: center; }
  .dish-list__item-price {
    padding-left: 17px; }
  .dish-list__item-image {
    width: 200px;
    height: 200px;
    display: inline-block;
    object-fit: cover;
    object-position: center center; }
    @media screen and (max-width: 768px) {
      .dish-list__item-image {
        width: 135px;
        max-width: 135px !important;
        min-height: 135px !important;
        max-height: 135px !important; } }
    .dish-list__item-image:after {
      content: "🙁 No dish image ";
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      font-style: normal;
      font-family: Roboto;
      position: absolute;
      left: 0;
      border: 1px #333333 dashed;
      background-color: #f3f3f3;
      color: #555;
      right: 0;
      top: 0;
      bottom: 0;
      text-align: center; }
  .dish-list__item-info {
    align-items: center;
    padding-right: 20px;
    width: 60%;
    flex-wrap: wrap;
    flex-grow: 1; }
    .dish-list__item-info-description {
      flex-grow: 1;
      width: 100%; }

.dish-list__item, .dish-list__item-info {
  display: flex;
  justify-content: space-between; }

.dish-list__item-description {
  padding-left: 20px;
  width: 100%;
  text-align: justify; }
